<template>
  <div>
      <div class="container">
        <h1>Tipo de Radar</h1>
      </div>
    <radar-type-table
        :data="radarTypeListState"
        @DeleteRadarType="DeleteRadarType"
        @addRadarType="addRadarType"
        @updateRadarType="updateRadarType"
    />
  </div>
</template>

<script>

import RadarTypeTable from "@/components/radarType/RadarTypeTable";
import {mapActions, mapGetters} from "vuex";
export default {
  name: "RadarType",
  components: {
    RadarTypeTable
  },
  data () {
    return {
      jsonDataUpdate: {
        abbreviation: '',
        autonomy: "",
        description: '',
        fuelPerHour: 0,
        fuelTotal: 0,
        high: 0,
        id: 0,
        length: 0,
        maker: 0,
        nominativo: "",
        operationCost: 0,
        photo: "",
        squadron: 0,
        status: 0,
        timesTammp: "",
        width: 0
      }
    }
  },
  created() {
    this.getRadarTypeState()
  },
  computed: {
    ...mapGetters(['radarTypeListState'])
  },
  methods: {
    ...mapActions(['getRadarTypeState', 'setRadarTypenew', 'apiUpdateRadarType', 'apiDeleteRadarType']),

    addRadarType( item) {
      this.setRadarTypenew(item)
    },
    updateRadarType( item) {
      this.jsonDataUpdate.abbreviation = item.abbreviation;
      this.jsonDataUpdate.autonomy = item.autonomy;
      this.jsonDataUpdate.description = item.description;
      this.jsonDataUpdate.fuelPerHour = item.fuelPerHour;
      this.jsonDataUpdate.fuelTotal = item.fuelTotal;
      this.jsonDataUpdate.high = item.high;
      this.jsonDataUpdate.id = item.id;
      this.jsonDataUpdate.length = item.length;
      this.jsonDataUpdate.maker = item.maker;
      this.jsonDataUpdate.nominativo = item.nominativo;
      this.jsonDataUpdate.operationCost = item.operationCost;
      this.jsonDataUpdate.photo = item.photo;
      this.jsonDataUpdate.squadron = item.squadron;
      this.jsonDataUpdate.status = item.status;
      this.jsonDataUpdate.timesTammp = item.timesTammp;
      this.jsonDataUpdate.width = item.width;
      
      this.apiUpdateRadarType(this.jsonDataUpdate)
    },
    DeleteRadarType(id) {
      this.apiDeleteRadarType(id)
    }
  }
}
</script>

